import { useWindowDimensions } from "components/App";
import React from "react";
import './style.css'

const cleanPercentage = (percentage) => {
  const tooLow = !Number.isFinite(+percentage) || percentage < 0;
  const tooHigh = percentage > 100;
  return tooLow ? 0 : tooHigh ? 100 : +percentage;
};

const Circle = ({ colour, pct, radius, strokewidth }) => {

  const colour2 = () => {
    let h = 350 - (pct*2) 
    let s = 70
    let l = 45

    let xyz = "hsl" + "(" + h + ", " + s + "%, " + l + "%" + ")"
    // console.log(xyz)

    return xyz
  }

  const colour1 = colour ? colour : colour2()

  const r = radius + (strokewidth / 2);
  const circ = 2 * Math.PI * r;
  const strokePct = ((100 - pct) * circ) / 100;
  return (
    <circle
      r={r}
      cx={0}
      cy={0}
      fill="transparent"
      stroke={strokePct !== circ ? colour1 : ""} // remove colour as 0% sets full circumference
      strokeWidth={strokewidth}
      strokeDasharray={circ}
      strokeDashoffset={pct ? strokePct : 0}
      strokeLinecap="round"
    ></circle>
  );
};

const Text = ({ percentage }) => {
  return (
    <text
      x="50%"
      y="50%"
      dominantBaseline="central"
      textAnchor="middle"
      fontSize={"1.5em"}
    >
      {percentage.toFixed(0)}%
    </text>
  );
};

const Pie = ({ percentage, colour, radius, strokewidth, text }) => {
  const pct = cleanPercentage(percentage);

  return (
    <svg className="pie_cirlce_container" width={(radius * 2) + (strokewidth * 5)} height={(radius * 2) + (strokewidth * 5)}>
      <g>
        {/* <Circle colour="lightgrey" radius={radius} strokewidth={strokewidth} /> */}
        <Circle pct={pct} radius={radius} strokewidth={strokewidth} />
      </g>
      {text && <Text percentage={pct} />}
    </svg>
  );
};

export default Pie;
