import './style.css'
import React, { useEffect, useState } from 'react'
import * as up_down_arrow from '../../../assets/up_down.svg'
import { useColourObjectStore } from '../../../store/ColourStore';


const Card = ({ classname, card_title, card_dropdown, card_preview, card_body, onexpand, expandable }) => {

  const { colourObj } = useColourObjectStore();

    const [card_open, setCard_open] = useState(true)

    return (
        <div className={classname ? 'dashboard_card ' + classname : 'dashboard_card'}>
            <div className='card_title' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m1 }}>
                {card_title}
                {card_dropdown}
                {card_preview}
                {expandable &&
                    <img className={card_open ?
                        'open_close_arrow_icon'
                        :
                        'open_close_arrow_icon rotate180'
                    } src={up_down_arrow} onClick={() => {
                        setCard_open(!card_open)
                        { !card_open && onexpand }
                    }} />
                }
            </div>
            {card_open &&
                <div className={card_title ?'card_details_wrapper' : 'card_details_wrapper_notitle'}>
                    {card_body}
                </div>
            }
        </div>
    )
}

export default Card;
