import React, { useEffect } from 'react'
import { useState } from 'react';
import './style.css'
import * as close_icon from '../../../assets/close_icon.svg'

import * as phone from '../../../assets/phone.png'
import { useColourObjectStore } from '../../../store/ColourStore';


const MobilePopup = (props: any) => {  

  const { title, desc1, desc2, popup_body, confirmClick, cancelClick, actionable } = props;

  const { colourObj } = useColourObjectStore();

  const [backendresponse_popup, setbackendresponse_popup] = useState(false);
  const [backendresponse, setbackendresponse] = useState('');
  const [popup_Title_Text, setpopup_Title_Text] = useState(true);
  const [popup_description_Text, setPopup_description_Text] = useState(true);
  const [popus_body_container, setPopus_body_container] = useState(true);

  useEffect(() => {
    // console.log(title, desc1, desc2, popup_body, confirmClick, cancelClick)
    // if (((desc1 === null) || (desc1 === undefined)) && ((desc2 === null) || (desc2 === undefined))) {
    //   setPopup_description_Text(false)
    // }
  }, [])

  return (
    <div className="popup_bg1">
      <div className="popup_container1">
        <div className="popup1">

          {backendresponse_popup ?
            <>
              <div className='title1'>{backendresponse}</div>

              <div className='popup_button_container1'>
                <div
                  onClick={() => {
                    console.log("***CANCEL***")
                    setbackendresponse_popup(false)
                  }}
                  className='popup_cancel_button1'>OK</div>
              </div>
            </>
            :
            <>
              {popup_Title_Text ?
                < div className='popup_title1' style={{ color: colourObj.color_m1 }}>
                  {title}
                </div>
                :
                null
              }

              {popup_description_Text ?
                <div className='popup_description1'>
                  <div className='popup_text1' style={{ color: colourObj.color_s1 }}>
                    {desc1}
                  </div>
                  <div className='popup_text1' style={{ color: colourObj.color_s1 }}>
                    {desc2}
                  </div>
                </div>
                :
                null
              }

              <div className="mobile_mockup_wrapper">
                <img className="mobile_mockup" src="https://comp2ass.s3.ap-south-1.amazonaws.com/logo_url/phone+(1).png" />
                {popus_body_container

                  ? popup_body
                  : null
                }
                <img className="close_icon" src={close_icon} onClick={confirmClick} />"
              </div>

            </>
          }
        </div>
      </div >
    </div>
  )
}

export default MobilePopup;


